@import url(https://fonts.googleapis.com/css?family=Lato:100,300&display=swap);
* {box-sizing: border-box;}
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  font-weight: 300 !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size:18px;
  color:white;
}

.container {
  height:100vh;
  width:100vw;
  background: rgb(29,80,250);
  background: linear-gradient(330deg, rgba(29,80,250,1) 18%, rgba(0,236,231,1) 99%);
  padding:20px;
  border-radius:5px;
}
/*--------Header.js styles----------*/
.header-style {
  background:rgba(0,0,0,0);
  height:80px ;
  width:100% ;
  padding:20px ;
  border-radius: 12px;
  margin-top:10px;
}

.header {
  text-align: center;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  margin: 0;
  color: white;
  font-size: 48px;
}
/*---------Search.js styles--------*/
.searchStyle {
  margin:8vh auto 0;
  color:white;
  font-size: 25px;
  text-align: center;
}
.searchButton {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  margin:10px auto;
  display: block;
  height: 45px;
  width: 285px;
  border:1px solid white;
  border-radius: 2px;
  color: white;
  background-color: #ffffff25;
  -webkit-transition: all 0.3s cubic-bezier(.08,.52,.52,1);
  transition: all 0.3s cubic-bezier(.08,.52,.52,1);
}
.city {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  height: 45px;
  width: 285px;
  margin-top:10px;
  -webkit-transition: all 0.3s cubic-bezier(.08,.52,.52,1);
  transition: all 0.3s cubic-bezier(.08,.52,.52,1);
}
.coord {
  margin:10px 0;
}
.searchButton:hover {
  background-color: #ffffff66;
  cursor: pointer;
}
.searchButton span {
  text-align: left;
}
.searchStyle div div input {
  float: right;
  border:none;
  height: 45px;
}
.searchStyle input {
  border:none;
  padding:0 10px;
  border-radius: 2px;
}

.findBtn:hover {
  background-color:#ffffff66 !important ;

}
/* --------Temp.js styles----------*/
.tempStyle {
  
  color:white;
  display: flex;
  flex-direction: column;
  width: 75%;
  margin:0 auto;
}

.tempStyle .temp {
  font-family: 'Lato', sans-serif;
  font-weight: 100;
  font-size: 25vh;
  text-align: center;
  flex:3 1;
  margin-bottom: 7px;
}

.tempStyle .others {
  font-size: 22px;
  text-align: center;
  flex: 1 1;
  margin-bottom: 20px;
}

.details {
  display:flex;
  background: #ffffff25;
  border-radius: 10px;
  height:16vh;  
  margin:0 auto 20px;
  padding:0px 0;
} 



.details div {
  text-align: center;
  flex: 1 1;
  padding: 1vh 0; 
}

.details div i {
  font-size: 30px;
  margin:10px;
}

input[type=number] {
  width:80px;
  height:30px;
  
}

@media screen and (min-width:1000px) {
  .details {
    width:950px;
  }
}

@media screen and (max-width:540px) {
  .container {
    width:100vw;
    height:100vh;
    margin: 0 !important;
    border-radius: 0;
  }
  .city, .searchButton {
    width: 75vw;
  }
  h2 {
    margin:0 !important;
    font-size: 20px;
  }
  .tempStyle .temp {
    font-size: 130px;
    font-family: 'Lato', sans-serif;
    font-weight: 100;
  }
  .details div i {
    font-size: 24px;
  }
  .details div span {
    font-size: 16px;
  }
}


